import './App.css';

function App() {
  var add_item = () => {
    var input = document.getElementById('input');
    var par = document.getElementById('parent');

    var x_btn = document.createElement('button');
    var li = document.createElement('li');

    if (input.value !== "") {
      li.className = "item";
      li.innerHTML = input.value;
      par.appendChild(li);

      x_btn.className = "delete";
      x_btn.innerHTML = 'X';
      li.appendChild(x_btn);
      input.value = "";
    }

    x_btn.addEventListener('click', () => {
      par.removeChild(li);
    })
  }

  window.addEventListener('keyup', (e) => {
    if (e.keyCode === 13) {
      add_item();
    }
  })




  return (
    <div className="main">

      <h2>Task 1</h2>

      <h3> To-do App</h3>
      <div className="container">
        <input type="text" id="input" />
        <button className='add' onClick={add_item}>Add</button>

        <ol id="parent">
        </ol>
      </div>

    </div>
  );
}

export default App;
